.Card {
  border: 1px solid #000;
  border-radius: 10px;
  padding: 8px;
  width: 80px;
  height: 130px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 8px;
  margin-left: 0;
}

.Shape {
  align-self: flex-start;
  padding: 4px;
}

.selected {
  background: #fffbbb;
}

.highlighted {
  background: #ffdbbb;
}
